body {
    padding-top: 56px; }

.clear:after {
    content: '';
    display: block;
    clear: both; }

.fl {
    float: left; }
.fr {
    float: right; }

.both-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY( -50% ); }

nav .brand-logo {
    font-weight: 100;
    font-size: 20px; }

.action-list {
    color: white;

    li {
        padding: 1em;
        border-bottom: 1px solid rgba( 255, 255, 255, 0.3 );

        &:first-child {
            padding-top: .5em; }

        &:last-child {
            border-bottom: 0;
            padding-bottom: .5em; } } }

#global-nav {
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 100; }

.nav-wrapper {
    background: white;

    #logo, .material-icons {
        color: black; } }

#cards {
    margin: 0;
    padding: 0 15px;
    list-style: none; }

.cd {
    width: 100%;
    border-radius: 10px;
    margin: 15px 0 15px;
    overflow: hidden; }

.cd-content {
    position: relative;
    padding: 1.5em;
    color: white; }

.quote {
    font-size: 2em;
    font-weight: bold;

    &:before {
        content: '"'; }

    &:after {
        content: '"'; } }

.desc {
    font-weight: bold; }

.desc2 {
    opacity: .9; }

.cd-footer {
    position: relative;
    overflow: hidden;
    padding: 1em;

    p {
        margin: 0; } }

.tr {
    text-align: right; }

.dis {
    color: white;
    opacity: .8; }

.cd-ui-container {
    padding: 0 1.5em 1em 1.5em;
    padding-top: 0;
    color: white; }

.domino {
    display: none;
    position: relative;
    overflow: hidden;

    .cd-content {
        position: relative;
        z-index: 0;
        min-height: 200px;

        h5 {
            margin: 0;
            font-weight: bold; }
        p {
            position: relative;
            margin: 0;
            z-index: 1; }

        .background {
            position: absolute;
            padding-top: 1em;
            top: 86px;
            right: -15px;
            margin-left: -110px;
            width: 210px;
            height: 92px;

            img {
                width: 100%;
                height: auto; } } } }


#apps {
    position: fixed;
    z-index: 150;
    background: black;
    opacity: 0.9;
    width: 100%;
    height: 0%;
    bottom: 0; }

ul.apps-list {
    color: white;
    margin: 0;
    padding: 0;
    list-style: none;
    width: 100%;
    position: relative;
    @extend .clear; }

.circle-item {
    width: 64px;
    height: 64px;
    line-height: 64px;
    border-radius: 32px;
    @extend .both-center; }

.app-item {
    position: relative;
    width: 33.333%;
    min-height: 128px;
    text-align: center;
    float: left;

    span {
        position: absolute;
        bottom: 0;
        width: 100%;
        left: 0; }

    .material-icons {
        font-size: 3rem;
        margin-top: 10px; } }

#close-apps {
    margin: 1em 1em 0 0;
    float: right;
    &:link, &:visited {
        color: white; } }
